import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux'
import { RootState } from '../reducers/index'
import { useTheme } from '@mui/material/styles';
import useMediaQuery from '@mui/material/useMediaQuery';
import {
    Grid,
    Button,
    TextField,
    InputAdornment,
    Paper,
    IconButton,
    Typography,
    Checkbox
} from '@material-ui/core';
import DeleteIcon from '@mui/icons-material/Delete';
import EditIcon from '@mui/icons-material/Edit';
import NavigateNextIcon from '@mui/icons-material/NavigateNext';
import Breadcrumbs from '@mui/material/Breadcrumbs';
import Link from '@mui/material/Link';
import Stepper from '@mui/material/Stepper';
import Step from '@mui/material/Step';
import StepLabel from '@mui/material/StepLabel';
import { makeStyles, createStyles, Theme } from '@material-ui/core/styles';
import web from '../apis/httpRequest'
import LabelIcon from '@mui/icons-material/Label';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import { useHistory } from 'react-router';
import { StepIconProps } from '@mui/material/StepIcon';
import CheckIcon from '@mui/icons-material/Check';
import AddIcon from '@mui/icons-material/Add';
import ArrowBackIosIcon from '@mui/icons-material/ArrowBackIos';
import _ from 'lodash'
import
{
    handleCreateAudienceTag,
    handleModifyAudienceTag,
    handleDeleteAudienceTag,
    fetchAudienceTag,
    handleAudienceFormForName,
    handleAudienceFormForTags,
    handleAudienceFormForSchedule,
    handleAudienceFormForTagsAfterEditTag
} from '../actions/audienceAction'
import { tagItem } from '../reducers/audienceReducer';
interface Props {
    children: JSX.Element | JSX.Element[];
}

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        main: {
            display: 'flex',
            width: '100%',
        },
        mainContainer:{
            width: '100%',
            backgroundColor:'#F1F4F6',
            height:'100vh'
        },
        serachInput: {
            [`& fieldset`]: {
                borderRadius: 5,
            },
        },
        selectBorder: {
            borderColor: "transparent !important"
        },
        nextButton: {
            borderRadius:5,
            color:'#FFF',
            backgroundColor:'#5A91FF',
            width:'120px',
            height:'40px',
            padding:'0 10px 0 10px',
            boxShadow:'none',
            '&:hover': {
                backgroundColor:'#5A91FF',
                boxShadow:'none'
            },
        },
        titleContainer: {
            width:'100%',
            backgroundColor:'#FFFFFF',
            padding: '30px 60px 0px 60px',
            height:'142px'
        },
        tab:{
            background: '#E3EEFF',
            boxShadow: '3px 0px 2px rgba(90, 145, 255, 0.2)',
            borderRadius: '10px 10px 0px 0px',
            width:'110px',
            height:'39px',
            padding: '5px 20px'
        },
        tabFont:{
            color:'#4A78DF',
            fontFamily: 'Noto Sans',
            fontStyle: 'normal',
            fontWeight: 'normal',
            fontSize: '16px',
            textAlign: 'center'

        },
        h1: {
            fontSize:'28px',
            color:'#000',
            fontWeight: 600,
            fontFamily:'Noto Sans'
        },
        h2: {
            fontSize:'14px',
            color:'#989898',
            fontWeight: 400,
            fontFamily:'Noto Sans'   
        },
        tagWord: {
            color: '#989898',
            fontSize: '12px',
            fontWeight: 400,
            fontFamily:'Noto Sans'   
        },
        tagButton: {
            borderRadius:5,
            color:'#FFF',
            backgroundColor:'#5A91FF',
            width:'80px',
            height:'32px',
            padding: '0 10px 0 10px',
            '&:hover': {
                backgroundColor:'#5A91FF',
            },
        },
    })
);

const MenuProps = {
  PaperProps: {
    style: {
      width: 250,
    },
  },
};

const baseTag = {id:'#base', name:'', color:'#5A91FF', createUser:'', createTime:''}
const templateTagForm = {id:'', name:'', color:'#5A91FF', createUser:'', createTime:''}

const Tags = (props:any) :React.ReactElement<Props>  =>  {

    const classes = useStyles()
    const history = useHistory()
    const theme = useTheme()
    const matchesMac = useMediaQuery(theme.breakpoints.down('xl'));
    const dispatch = useDispatch()
    const tags = useSelector((state: RootState) => state.audienceTag)
    const audienceForm = useSelector((state: RootState) => state.audienceForm)
    const [createTagOpen, setCreateTagOpen] = useState(false)
    const [editTagOpen, setEditTagOpen] = useState(false)
    const [tagForm, setTagForm] = useState<tagItem>(templateTagForm)
    const [mouseHoverTag, setMouseHoverTag] = useState('')

    const color = [
        '#5A91FF', '#1DC0D7', '#8C7CEE', '#FEC558', '#52C41A', '#FF83F3', '#989898', '#0E0F0F'
    ]

    useEffect(() =>
    {
        dispatch(fetchAudienceTag())
    },[])
    

    return(
        <Grid item style={{marginRight:'20px'}}>
            <FormControl fullWidth>
                <Select
                    startAdornment={
                        <InputAdornment position="start">
                            <LabelIcon fontSize={'small'} style={{color:'#000'}}/>
                        </InputAdornment>
                    }
                    IconComponent={() => (
                        <React.Fragment></React.Fragment>
                    )}
                    variant='standard'
                    disableUnderline
                    labelId="demo-multiple-name-label"
                    id="demo-multiple-name"
                    multiple
                    disabled={props.disable}
                    style={{paddingLeft:'10px',width: matchesMac?'220px':'380px',height:'40px',color:'#0E0F0F',fontSize:'14px',backgroundColor:'#EDEDED',borderRadius:5, fontFamily:'Noto Sans'}}
                    value={props.isLoading ? [] :audienceForm.tags.map(x=>x.id)}
                    renderValue={(value) =>
                    {
                        return (
                            <Grid container>
                                {value.map(ta =>
                                {
                                    const specificTag = tags.filter(x => x.id === ta)[0]
                                    const tagColor = specificTag?.color
                                    const tagName = specificTag?.name
                                    return <Grid item key={ta} style={{ height: '24px', backgroundColor: tagColor, marginRight: '10px', padding: '0px 8px', borderRadius: 5 }}> 
                                        <Grid container style={{ height: '24px' }} justifyContent='center' alignItems='center'>
                                            <Grid item>
                                                <Typography style={{color:'#FFF',fontSize:'14px',fontFamily:'Noto sans', WebkitTextFillColor:'#FFF'}}> {`#${tagName}`} </Typography>
                                            </Grid>
                                        </Grid>
                                    </Grid>
                                })}
                            </Grid>
                        )
                    }}
                    MenuProps={MenuProps}
                >
                    {createTagOpen ? 
                        <Grid container direction='column' style={{ width: '100%'}}>
                            <Grid item container justifyContent='space-between' alignItems='center' style={{padding:'0px 20px 0px 10px'}}>
                                <Grid item>
                                    <Grid container alignItems='center'>
                                        <IconButton style={{padding:0}} onClick={()=> { setCreateTagOpen(false); setTagForm(templateTagForm) }}>
                                            <ArrowBackIosIcon style={{color:'#0E0F0F',width:'20px',height:'20px'}}/>
                                        </IconButton>
                                        <Typography style={{color:'#0E0F0F',fontSize:'18px',fontFamily:'Noto sans'}}>{'Add new tag'}</Typography>
                                    </Grid>
                                </Grid>
                                <Grid item>
                                    <Grid container justifyContent='center' alignItems='center'>
                                        {
                                            tagForm.name === '' ? 
                                                <Grid item container justifyContent='center' alignItems='center' style={{width:'20px',height:'16px',backgroundColor:'#E0E0E0',borderRadius: '5px'}}>
                                                    <Typography style={{color:'#FFF',fontSize:'12px'}}>{'#'}</Typography>
                                                </Grid>
                                                :
                                            <Grid item key={tagForm.name} style={{  height: '21px',padding:'0px 6px', backgroundColor: tagForm.color, borderRadius: 5 }}> 
                                                <Grid container  justifyContent='center' alignItems='center'>
                                                    <Typography style={{color:'#FFF',fontSize:'14px',fontFamily:'Noto sans'}}> {`${tagForm.name}`} </Typography>
                                                </Grid>
                                            </Grid>
                                        }
                                    </Grid>
                                </Grid>
                            </Grid>
                            <Grid item style={{width:'100%',height:'1px',marginTop:'8px',backgroundColor:'#E0E0E0'}}>

                            </Grid>
                            <Grid item container direction='column' style={{paddingLeft:'20px'}}>
                                <Grid item style={{marginTop:'20px'}}>
                                    <Typography className={classes.tagWord}>{'Tag name'}</Typography>
                                </Grid>
                                <Grid item style={{marginTop:'8px'}}>
                                    <TextField
                                        placeholder='Tag name'
                                        variant={'outlined'}
                                        value={tagForm.name}
                                        className={classes.serachInput}
                                        InputProps={{
                                            type: 'input',
                                            style: {
                                                height: '40px',
                                                fontSize: '15px',
                                                width: matchesMac?'95%':'280px',
                                                padding: '0px 0px 0px 10px',
                                                backgroundColor: '#F3F3F3',
                                                marginRight: '20px'
                                            },
                                            classes: {
                                                notchedOutline: classes.selectBorder
                                            }
                                        }}
                                        onChange={(event) => setTagForm({id:'', name: event.target.value, color: tagForm.color, createUser: '', createTime: ''})}
                                        size="small"
                                    />
                                </Grid>
                                <Grid item style={{marginTop:'20px'}}>
                                    <Typography className={classes.tagWord}>{'Tag style'}</Typography>
                                </Grid>
                                <Grid item container style={{marginTop:'12px'}}>
                                    {
                                        color.map(tagColor =>
                                        {
                                            return (
                                                <Grid key={tagColor} item style={{ marginRight: '10px' }} onClick={() => setTagForm({...tagForm, color: tagColor})}>
                                                        <Grid container justifyContent='center' alignItems='center' style={{ width: '30px', height: '30px', borderRadius: 50, border: tagColor === tagForm.color ? '1px solid #0E0F0F' : ''}}>       
                                                        <Grid container alignItems='center' justifyContent='center' style={{ width: '24px', height: '24px', backgroundColor: tagColor, borderRadius: 50 }}>
                                                            <IconButton disableRipple style={{ padding: 0 }}>
                                                                <Typography style={{color:'#FFF'}}>{'A'}</Typography>
                                                            </IconButton>
                                                        </Grid>
                                                    </Grid>
                                                </Grid>
                                            )
                                        })
                                    }
                                </Grid>
                            </Grid>
                                <Grid item style={{width:'100%',height:'1px',marginTop:'20px',backgroundColor:'#E0E0E0'}}>

                            </Grid>
                            <Grid container justifyContent='flex-end' style={{marginTop:'10px'}}>
                                <Grid item style={{marginRight:'16px'}}>
                                    <Button onClick={() => { setCreateTagOpen(false); setTagForm(templateTagForm) }} variant="contained" disableRipple style={{boxShadow:'none',border: '2px solid #5A91FF',backgroundColor:'#F1F4F6',color:'#5A91FF'}} className={classes.tagButton} >{'Cancel'}</Button>
                                </Grid>
                                <Grid item style={{marginRight:'20px'}}>
                                    <Button onClick={() =>
                                    {
                                        const res = dispatch(handleCreateAudienceTag(tagForm));
                                        Promise.resolve(res).then((re:any) =>
                                        {
                                            if (re.status === 200)
                                            {
                                                dispatch(fetchAudienceTag())
                                                setTagForm(templateTagForm)
                                            }
                                            else
                                            {
                                                alert('Create tags failed')    
                                            }
                                        })
                                        setCreateTagOpen(false)
                                    }} variant="contained" style={{ boxShadow: 'none' }} disableRipple className={classes.tagButton} >{'Done'}</Button>
                                </Grid>
                            </Grid>
                            </Grid>
                        : editTagOpen ?
                            <Grid container direction='column' style={{ width: '100%'}}>
                                <Grid item container justifyContent='space-between' alignItems='center' style={{padding:'0px 20px 0px 10px'}}>
                                    <Grid item>
                                        <Grid container alignItems='center'>
                                            <IconButton style={{padding:0}} onClick={()=> { setEditTagOpen(false); setTagForm(templateTagForm) }}>
                                                <ArrowBackIosIcon style={{color:'#0E0F0F',width:'20px',height:'20px'}}/>
                                            </IconButton>
                                            <Typography style={{color:'#0E0F0F',fontSize:'18px',fontFamily:'Noto sans'}}>{'Edit tag'}</Typography>
                                        </Grid>
                                    </Grid>
                                    <Grid item>
                                        <Grid container justifyContent='center' alignItems='center'>
                                            {
                                                tagForm.name === '' ? 
                                                    <Grid item container justifyContent='center' alignItems='center' style={{width:'20px',height:'16px',backgroundColor:'#E0E0E0',borderRadius: '5px'}}>
                                                        <Typography style={{color:'#FFF',fontSize:'12px'}}>{'#'}</Typography>
                                                    </Grid>
                                                :
                                                <Grid item key={tagForm.name} style={{  height: '21px',padding:'0px 6px', backgroundColor: tagForm.color, borderRadius: 5 }}> 
                                                    <Grid container  justifyContent='center' alignItems='center'>
                                                        <Typography style={{color:'#FFF',fontSize:'14px',fontFamily:'Noto sans'}}> {`${tagForm.name}`} </Typography>
                                                    </Grid>
                                                </Grid>
                                            }
                                        </Grid>
                                    </Grid>
                                </Grid>
                                <Grid item style={{width:'100%',height:'1px',marginTop:'8px',backgroundColor:'#E0E0E0'}}>

                                </Grid>
                                <Grid item container direction='column' style={{paddingLeft:'20px'}}>
                                    <Grid item style={{marginTop:'20px'}}>
                                        <Typography className={classes.tagWord}>{'Tag name'}</Typography>
                                    </Grid>
                                    <Grid item style={{marginTop:'8px'}}>
                                        <TextField
                                            placeholder='Tag name'
                                            variant={'outlined'}
                                            className={classes.serachInput}
                                            value={tagForm.name}
                                            onChange={(event) => setTagForm({...tagForm, name: event.target.value})}
                                            InputProps={{
                                                type: 'input',
                                                style: {
                                                    height:'40px',
                                                    fontSize:'15px',
                                                    width: matchesMac?'95%':'280px',
                                                    padding:'0px 0px 0px 10px',
                                                    backgroundColor:'#F3F3F3',
                                                    marginRight:'20px'
                                                },
                                                classes: {
                                                    notchedOutline: classes.selectBorder
                                                }
                                            }}
                                            size="small"
                                        />
                                    </Grid>
                                    <Grid item style={{marginTop:'20px'}}>
                                        <Typography className={classes.tagWord}>{'Tag style'}</Typography>
                                    </Grid>
                                    <Grid item container style={{marginTop:'12px'}}>
                                        {
                                            color.map(tagColor =>
                                            {
                                                return (
                                                    <Grid key={tagColor} item style={{ marginRight: '10px' }} onClick={() => setTagForm({ ...tagForm, color: tagColor })}>
                                                        <Grid container justifyContent='center' alignItems='center' style={{ width: '30px', height: '30px', borderRadius: 50, border: tagColor === tagForm.color  ? '1px solid #0E0F0F' : ''}}>       
                                                            <Grid container alignItems='center' justifyContent='center' style={{ width: '24px', height: '24px', backgroundColor: tagColor, borderRadius: 50 }}>
                                                                <IconButton disableRipple style={{ padding: 0 }} >
                                                                    <Typography style={{color:'#FFF'}}>{'A'}</Typography>
                                                                </IconButton>
                                                            </Grid>
                                                        </Grid>
                                                    </Grid>
                                                )
                                            })
                                        }
                                    </Grid>
                                </Grid>
                                <Grid item style={{width:'100%',height:'1px',marginTop:'20px',backgroundColor:'#E0E0E0'}}>

                                </Grid>
                                <Grid container justifyContent='flex-end' style={{marginTop:'10px'}}>
                                    <Grid item style={{marginRight:'16px'}}>
                                        <Button onClick={()=> { setEditTagOpen(false); setTagForm(templateTagForm) }} variant="contained" disableRipple style={{boxShadow:'none',border: '2px solid #5A91FF',backgroundColor:'#F1F4F6',color:'#5A91FF'}} className={classes.tagButton} >{'Cancel'}</Button>
                                    </Grid>
                                    <Grid item style={{marginRight:'20px'}}>
                                        <Button onClick={() =>
                                        {
                                            const res = dispatch(handleModifyAudienceTag(tagForm)); 
                                            Promise.resolve(res).then((re:any) =>
                                            {
                                                if (re.status === 200)
                                                {
                                                    dispatch(fetchAudienceTag())
                                                    dispatch(handleAudienceFormForTagsAfterEditTag(tagForm))
                                                    setTagForm(templateTagForm)
                                                }
                                                else
                                                {
                                                    alert('Modify tags failed')    
                                                }
                                            })
                                            setEditTagOpen(false)
                                        }} variant="contained" style={{ boxShadow: 'none' }} disableRipple className={classes.tagButton} >{'Done'}</Button>
                                    </Grid>
                                </Grid>
                            </Grid>
                        :
                            [baseTag,...tags].map(tag =>
                            {
                                if (tag.id === '#base')
                                {
                                    return (
                                        <MenuItem key={tag.name} onClick={()=> setCreateTagOpen(true)}>
                                            <Grid item container alignItems='center'>
                                                <AddIcon style={{ width:'20px',height:'20px',color: '#5A91FF',marginRight:'9px' }}/>
                                                <Grid item>
                                                    <Typography style={{color:'#5A91FF', fontFamily:'Noto sans', fontSize:'14px'}}>{'Create a new tag'}</Typography>
                                                </Grid>
                                            </Grid>
                                        </MenuItem>
                                    )
                                }
                                return (
                                    <Grid item>
                                        <MenuItem value={tag.name} style={{ paddingLeft: '14px' }} onClick={() => dispatch(handleAudienceFormForTags(tag))} onMouseEnter={()=> setMouseHoverTag(tag.name)} onMouseLeave={()=> setMouseHoverTag('')}>
                                            <Grid container justifyContent='space-between' alignItems='center'>
                                                <Grid item>
                                                    <Grid container alignItems='center'>
                                                        {_.includes(audienceForm.tags.map(x => x.id), tag.id) === true ? <CheckIcon style={{ width: '20px', height: '20px', color: '#5A91FF', marginRight: '9px' }} /> : <Grid item style={{ width: '20px', height: '20px', marginRight: '9px' }}></Grid>}
                                                        <Typography>{tag.name}</Typography>
                                                    </Grid>
                                                </Grid>
                                                {
                                                    mouseHoverTag === tag.name ? 
                                                        <Grid item>
                                                            <Grid container>
                                                                <IconButton style={{ padding: 0, marginRight: '4px' }} onClick={(event) =>
                                                                {
                                                                    event.stopPropagation();
                                                                    const res = dispatch(handleDeleteAudienceTag(tag))
                                                                    Promise.resolve(res).then((re:any) =>
                                                                    {
                                                                        if (re.status === 200)
                                                                        {
                                                                            dispatch(fetchAudienceTag())
                                                                            dispatch(handleAudienceFormForTags(tag))
                                                                            setTagForm(templateTagForm)
                                                                        }
                                                                        else
                                                                        {
                                                                            alert('Delete tags failed')    
                                                                        }
                                                                    })
                                                                }}>
                                                                    <DeleteIcon style={{ color: '#989898' }} />
                                                                </IconButton>
                                                                <IconButton style={{ padding: 0 }} onClick={(event) => { event.stopPropagation(); setTagForm(tag); setEditTagOpen(true) }}><EditIcon style={{color:'#989898'}}/></IconButton>
                                                            </Grid>
                                                        </Grid>
                                                    : ''
                                                }
                                            </Grid>
                                        </MenuItem>
                                    </Grid>
                                    
                                )
                            })
                    }
                </Select>
            </FormControl>
        </Grid>
    )
}

export default Tags