export const handleCreateAudienceTag = (tag: tagItem) => async (dispatch: any) =>
{
    const data = {
        name: tag.name,
        color: tag.color,
        createUser: tag.createUser
    }

    const payload = await web.post(`/v1/audiences/tags`, data)
    return payload
}

export const handleModifyAudienceTag = (tag: tagItem) => async (dispatch: any) =>
{
    const data = {
        name: tag.name,
        color: tag.color,
        createUser: tag.createUser
    }
    const payload = await web.put(`/v1/audiences/tags/${tag.id}`, data)
    return payload
}

export const handleDeleteAudienceTag = (tag: tagItem) => async (dispatch: any) =>
{
    const payload = await web.delete(`/v1/audiences/tags/${tag.id}`)
    return payload
}

export const fetchAudienceTag = () => async (dispatch: any) =>
{
    const payload = await web.get(`/v1/audiences/tags`)

    dispatch({
        type:FETCH_AUDIENCE_TAG,
        payload: payload.data
    })
}

export const handleConfirmChecked = (id: string) => async (dispatch: any) =>
{
    const payload = await web.post(`/v1/audiences/${id}/confirms`)
    return payload
}

export const handleMutipleConfirmChecked = (idList: string[]) => async (dispatch: any) =>
{
    return Promise.all(idList.map(id => web.post(`/v1/audiences/${id}/confirms`)))
}

export const fetchSpecificAudience = (id: string) => async (dispatch: any) =>
{
    dispatch({ type: AUDIENCE_LOADING, payload: true })
 
    const payload = await web.get<AudienceType>(`/v1/audiences/${id}`)

    if (payload.status === 200)
    {
        dispatch({
            type: FETCH_SPECIFIC_AUDIENCE,
            payload: payload.data
        })
    }

    const form = {
        name: payload.data.name,
        banner: payload.data.details.banner,
        filtering: payload.data.details.filtering,
        tags: payload.data.tags,
        schedule: payload.data.details.schedule,
        actionDestination: payload.data.details.actionDestination
    }
        
    dispatch({ type: INIT_AUDIENCE_FORM, payload: form })
    dispatch({ type: AUDIENCE_LOADING, payload: false })
}

export const handleSpecificAudienceIntoForm = (audience: AudienceType) => async (dispatch: any) =>
{
    const payload = await web.get<AudienceType>(`/v1/audiences/${audience.id}`)

    if (payload.status === 200)
    {
        dispatch({
            type: FETCH_SPECIFIC_AUDIENCE,
            payload: payload.data
        })
    }
    const form = {
        name: audience.name,
        banner: audience.details.banner,
        filtering: audience.details.filtering,
        tags: audience.tags,
        schedule: audience.details.schedule,
        actionDestination: audience.details.actionDestination
    }
        
    dispatch({ type: INIT_AUDIENCE_FORM, payload: form })
}

export const unmount_audience_details = () => async (dispatch: any) =>
{
    dispatch({ type: UNMOUNT_AUDIENCE_FORM, payload: {} })
    return true
}


export const fetchAudience = () => async (dispatch:any) => {

    dispatch({ type: AUDIENCE_LOADING, payload: true })
    
    const payload = await web.get<AudienceType[]>(`/v1/audiences`)

    if (payload.status === 200)
    {
        dispatch({
            type: FETCH_AUDIENCE,
            payload: payload.data
        })
    }

    dispatch({ type: AUDIENCE_LOADING, payload: false })
    return payload
}

export const duplicatedAudience = (audience: AudienceType) => async (dispatch: any) =>
{
    const randomChar = Math.random().toString(36).slice(2, 7)
    const data = {
        name: `${audience.name}_${randomChar}`,
        confirm: false,
        details: audience.details,
        lastUpdatedBy: audience.lastUpdatedBy,
        tags: audience.tags.map(x=> x.id)
    }

    const payload = await web.post(`/v1/audiences`, data)
    return payload
}

export const createAudience = (audience: audienceForm) => async (dispatch:any) => {

    const body = {
        name: audience.name,
        lastUpdatedBy: 'testAccount',
        tags: audience.tags.map(x => x.id),
        details: {
            filtering: audience.filtering.map((audi:creatorType, index:number) =>
            {
                if (index === audience.filtering.length - 1)
                {
                    return  {...audi, ...{connector: ''}}
                }
                else
                {
                    return audi    
                }
            }),
            banner: audience.banner,
            schedule: audience.schedule,
            actionDestination: audience.actionDestination
        }
    }

    const payload = await web.post<audienceForm>(`/v1/audiences`,body)
    return payload
}

export const modifyAudience = (audience: audienceForm, originalData:AudienceType) => async (dispatch:any) => {

    const body = {
        confirm: originalData.confirm,
        name: audience.name,
        lastUpdatedBy: 'testAccount',
        tags: audience.tags.map(x => x.id),
        details: {
            filtering: audience.filtering.map((audi:creatorType, index:number) =>
            {
                if (index === audience.filtering.length - 1)
                {
                    return  {...audi, ...{connector: ''}}
                }
                else
                {
                    return audi    
                }
            }),
            banner: audience.banner,
            schedule: audience.schedule,
            actionDestination: audience.actionDestination
        }
    }
    
    const payload = await web.put(`/v1/audiences/${originalData.id}`,body)
    return payload
}

