export const handleAudienceChangeForName = (creator: creatorType, filter: filterColumn, newFilter: any) => async (dispatch: any) =>
{
    const modifyFilter = {
        filterId: filter.filterId,
        type: newFilter.type,
        filterName: newFilter.name,
        value: newFilter.value,
        operator: _.keys(newFilter.operator)[0],
        optionValues: {},
        subFilterName: '',
        subValue: ''
    }
    const payload = { ...creator, filterColumn: _.sortBy([...creator.filterColumn.filter(x => x.filterId !== filter.filterId), modifyFilter],'filterId')}
    
    dispatch({
        type: HANDLE_AUDIENCE_FORM_FILTERING_CHANGE,
        payload: payload
    })
}


export const handleAudienceChangeForOperator = (creator: creatorType, filter: filterColumn, operator: string) => async (dispatch: any) =>
{
    const selectedOperator = audienceSelectorMapping[creator.value].filter((x: any) => x.value === filter.value)[0]?.operator[operator]
    const modifyFilter = { ...filter, operator: operator, type: selectedOperator?.type}
    const payload = { ...creator, filterColumn: _.sortBy([...creator.filterColumn.filter(x => x.filterId !== filter.filterId), modifyFilter],'filterId')}

    dispatch({
        type: HANDLE_AUDIENCE_FORM_FILTERING_CHANGE,
        payload: payload
    })
}


export const handleAudienceChangeForValues = (creator: creatorType, filter: filterColumn, value: any, mutipleValue?: number | undefined) => async (dispatch: any) =>
{
    let optionValues: optionValuesType = { value1: '', value2: '' }
    if (mutipleValue !== undefined)
    {
        const tempList = filter.optionValues
        if (mutipleValue === 0)
        {
            tempList.value1 = value
        }
        else
        {
            tempList.value2 = value
        }
        optionValues = tempList
    }
    else
    {
        optionValues.value1 = value
    }

    const modifyFilter = { ...filter, optionValues: optionValues }

    const payload = { ...creator, filterColumn: _.sortBy([...creator.filterColumn.filter(x => x.filterId !== filter.filterId), modifyFilter], 'filterId') }

    dispatch({
        type: HANDLE_AUDIENCE_FORM_FILTERING_CHANGE,
        payload: payload
    })
}



export const handleAudienceChangeForNameMutidimension  = (creator: creatorType, filter: filterColumn, newFilter: any) => async (dispatch: any) =>
{
    const modifyFilter = {
        filterId: filter.filterId,
        type: newFilter.type,
        filterName: newFilter.name,
        value: newFilter.value,
        operator:  newFilter.subname === undefined? '' : _.keys(newFilter.subname[_.keys(newFilter.subname)[0]]?.operator)[0],
        optionValues: {},
        subFilterName: newFilter.subname === undefined? '' :newFilter.subname[_.keys(newFilter.subname)[0]]?.name,
        subValue:  newFilter.subname === undefined? '' :newFilter.subname[_.keys(newFilter.subname)[0]]?.value
    }
    const payload = { ...creator, filterColumn: _.sortBy([...creator.filterColumn.filter(x => x.filterId !== filter.filterId), modifyFilter],'filterId')}
    
    dispatch({
        type: HANDLE_AUDIENCE_FORM_FILTERING_CHANGE,
        payload: payload
    })
}

export const handleAudienceChangeForSubNameMutidimension  = (creator: creatorType, filter: filterColumn, subname: string) => async (dispatch: any) =>
{
    const mappings = audienceSelectorMapping[creator.value].filter((x: any) => x.value === filter.value)[0]?.subname[subname]
    const modifyFilter = {
        ...filter,
        subFilterName: mappings.name,
        subValue: mappings.value,
        operator: _.keys(mappings.operator)[0],
        type: mappings.operator[_.keys(mappings.operator)[0]].type,
        optionValues: {},
    }

    const payload = { ...creator, filterColumn: _.sortBy([...creator.filterColumn.filter(x => x.filterId !== filter.filterId), modifyFilter],'filterId')}
    
    dispatch({
        type: HANDLE_AUDIENCE_FORM_FILTERING_CHANGE,
        payload: payload
    })
}

