import { useDispatch, useSelector } from 'react-redux'
import { RootState } from '../reducers/index'
import { useTheme } from '@mui/material/styles';
import useMediaQuery from '@mui/material/useMediaQuery';
import {
    Grid,
    TextField,
    InputAdornment,
    Typography,
} from '@material-ui/core';
import { makeStyles, createStyles, Theme } from '@material-ui/core/styles';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import { useHistory } from 'react-router';
import _ from 'lodash'
import
{
    fetchAudienceTag,
    handleAudienceFormForName,
    handleAudienceFormForBanner
} from '../actions/audienceAction'
import { bannerMappings } from '../utils/tool'
import TurnedInIcon from '@mui/icons-material/TurnedIn';

interface Props {
    children: JSX.Element | JSX.Element[];
}

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        main: {
            display: 'flex',
            width: '100%',
        },
        mainContainer:{
            width: '100%',
            backgroundColor:'#F1F4F6',
            height:'100vh'
        },
        serachInput: {
            [`& fieldset`]: {
                borderRadius: 5,
            },
        },
        selectBorder: {
            borderColor: "transparent !important"
        },
        nextButton: {
            borderRadius:5,
            color:'#FFF',
            backgroundColor:'#5A91FF',
            width:'120px',
            height:'40px',
            padding:'0 10px 0 10px',
            boxShadow:'none',
            '&:hover': {
                backgroundColor:'#5A91FF',
                boxShadow:'none'
            },
        },
        titleContainer: {
            width:'100%',
            backgroundColor:'#FFFFFF',
            padding: '30px 60px 0px 60px',
            height:'142px'
        },
        tab:{
            background: '#E3EEFF',
            boxShadow: '3px 0px 2px rgba(90, 145, 255, 0.2)',
            borderRadius: '10px 10px 0px 0px',
            width:'110px',
            height:'39px',
            padding: '5px 20px'
        },
        tabFont:{
            color:'#4A78DF',
            fontFamily: 'Noto Sans',
            fontStyle: 'normal',
            fontWeight: 'normal',
            fontSize: '16px',
            textAlign: 'center'

        },
        h1: {
            fontSize:'28px',
            color:'#000',
            fontWeight: 600,
            fontFamily:'Noto Sans'
        },
        h2: {
            fontSize:'14px',
            color:'#989898',
            fontWeight: 400,
            fontFamily:'Noto Sans'   
        },
        tagWord: {
            color: '#989898',
            fontSize: '12px',
            fontWeight: 400,
            fontFamily:'Noto Sans'   
        },
        tagButton: {
            borderRadius:5,
            color:'#FFF',
            backgroundColor:'#5A91FF',
            width:'80px',
            height:'32px',
            padding: '0 10px 0 10px',
            '&:hover': {
                backgroundColor:'#5A91FF',
            },
        },
    })
);

const MenuProps = {
  PaperProps: {
    style: {
      width: 250,
    },
  },
};

interface audienceBarProps
{
    disable: boolean
}

const AudeinceBar = (props:audienceBarProps) :React.ReactElement<Props>  =>  {

    const classes = useStyles()
    const history = useHistory()
    const theme = useTheme()
    const matchesMac = useMediaQuery(theme.breakpoints.down('xl'));
    const dispatch = useDispatch()
    const audienceForm = useSelector((state: RootState) => state.audienceForm)

    useEffect(() =>
    {
        dispatch(fetchAudienceTag())
    },[])
    
    const bannerData = [
        { key: 'KFC', name: 'KFC', value: 'KFC' },
        { key: 'PHHK', name: 'Pizza Hut', value: 'PHHK' },
        { key: 'PHDHK', name: 'PHD', value: 'PHDHK' }
    ]

    return(
        <>
            <Grid item >
                <TextField
                    disabled={props.disable}
                    value={audienceForm.name}
                    onChange={(event)=> dispatch(handleAudienceFormForName(event.target.value))}
                    placeholder='Audience name'
                    variant={'outlined'}
                    className={classes.serachInput}
                    InputProps={{
                        type: 'input',
                        style: {
                            height:'40px',
                            fontSize:'15px',
                            width: matchesMac?'200px':'330px',
                            padding:'0px 0px 0px 10px',
                            backgroundColor:'#F3F3F3',
                            marginRight:'20px'
                        },
                        classes: {
                            notchedOutline: classes.selectBorder
                        }
                    }}
                    size="small"
                />
            </Grid>
            <Grid item  style={{marginRight:'20px'}}>
                <FormControl>
                    <Select
                        disabled={props.disable}
                        startAdornment={
                            <InputAdornment position="start">
                                <TurnedInIcon fontSize={'small'} style={{color:'#000'}}/>
                            </InputAdornment>
                        }
                        variant='standard'
                        disableUnderline
                        labelId="demo-simple-select-helper-label"
                        id="demo-simple-select-helper"
                        value={audienceForm.banner}
                        onChange={(event)=> dispatch(handleAudienceFormForBanner(event.target.value))}
                        style={{padding:'0px 10px',width: matchesMac?'130px':'160px',height:'40px',color:'#0E0F0F',fontSize:'14px',borderRadius:5, backgroundColor:'#EDEDED', fontFamily:'Noto Sans'}}
                        renderValue={(selected) =>
                        {
                            const newBanner = bannerMappings[selected]
                            return (
                                <Grid container alignItems='center'>
                                    <Grid item key={newBanner}>
                                        <Grid container alignItems='center'>
                                            <Grid item>
                                                <Typography style={{ fontSize: '14px', fontFamily: 'Noto sans' }}>{newBanner}</Typography>
                                            </Grid>
                                        </Grid>
                                    </Grid>
                                </Grid>
                            )
                        }}
                    >
                    {
                        bannerData.map(ban =>
                        {
                            return (
                                <MenuItem key={ban.value} value={ban.value} style={{width: matchesMac?'130px':'160px',marginRight:'-30px'}}>
                                    <Grid container alignItems='center'>
                                        <Grid item>
                                            <Typography style={{ fontSize: '14px', fontFamily: 'Noto sans' }}>{ban.name}</Typography>
                                        </Grid>
                                    </Grid>
                                </MenuItem>
                            )
                        })
                    }
                    </Select>
                </FormControl>
            </Grid>
        </>
    )
}

export default AudeinceBar